  const [gameEnded, setGameEnded] = React.useState(false)

  React.useEffect(() => {
    setPickedWord(targetWords[Math.floor(Math.random() * targetWords.length)])
  }, [])

  function startNewGame() {
    // console.log("Starting new game...")
    window.location.reload()
  }

